import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby } from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./styles.css"
import { AddEntry_Cmpt, EntryItem_Cmpt, AddComprobanteManual_Cmpt } from "..";
import { getall_entries, search_entries } from "../services/server_fetching";
import { getall_subaccount } from "../../CuentasContables/services/server_fetching";
import { useNavigate } from "react-router";



const SearchInput = SearchInput_Cmpt();
const AddAccount = AddEntry_Cmpt();


const AccountItem = EntryItem_Cmpt();
const AddComprobanteManual = AddComprobanteManual_Cmpt();




const AccountDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [addview, setAddview] = useState(false);
	const [updview, setUpdview] = useState(false);
	const [loading, set_loading] = useState(false);

	const navigate = useNavigate();



	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			getall_subaccount(cb_result);
			
		}
	});


	const openUpdateView =  () => {
		setAddview(false)
		setUpdview(true)
	}


	 
	const goToComprob =  () => {
		navigate({
				pathname: "/accounting-ledger"
		});
		
	}

	const openAddManual =  () => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true,  };
		dataModal["content"] = <AddComprobanteManual confirm={goToComprob} />
		OpenModal(dataModal);
	}




	
	const openAddView =  () => {
		setUpdview(false)
		//setAddview(true)
		let data = {};
		data["zIndex"] = 450;
		data["height"] = "80vh";
		// data['observeResize']=true;
		data["props"] = { minHeight: "1vh", overlay: true };
		data["content"] = 
			<AddAccount confirm={handle_getall_entries} />
		
		OpenModal(data);
	}


	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_account =  (v) => {
		set_loading(true)
		search_entries(cb_result)
	}


	const handle_getall_entries =  (v) => {
		getall_entries(cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	

	return (

	<div className="">
		<div className="accounts_header">
			<h2>Comprobantes Parametrizados</h2>
			<div className="_dsplFlx ">
				<SearchInput
					placeholder={"Buscar"} 
					width={360}
					obs={set_obs}
					fetchData={handle_search_account}
					loading={loading}
					change={handleChange}
					callempty={handle_getall_entries}
					keyFlds={"search_entry_qry"}
				/>
				<p className="group_title addBtn purple" onClick={openAddView}>
					Parametrizar
				</p>
				<p className="group_title addBtn purple" onClick={openAddManual}>
						Agregar Comprobante Manual
				</p>
			</div>
		</div>

		
	
		{!updview && !addview && 
			
			<div className="accounts_container scollVh">
				{getStore("entries_list") && ObjectKeys(getStore("entries_list"))?.map(cid=>{
					return (
						<AccountItem 
							data={getStore("entries_list")[cid]} 
							elmId={cid} 
							key={cid}
							openUpdateView={openUpdateView}
							handleRefreshAll={handle_getall_entries}
						/>
					)
				})}
			</div>
		}
		</div>
	);
};

export default AccountDashboard;

